/* Titles for my projects */
.project-title {
  text-decoration: underline;
}

/* Stunning image */
.cell-portfolio {
  float: left;
  margin: 5px 25px 50px 0px;
  object-fit: cover;
  width: 100%;
  height: 400px;
  border-radius: 20px;
  object-position: 0px 5%;
}

.cell-a {
  font-family: 'EB Garamond', serif;
  color: #0299df;
}

.cell-a:hover {
  text-decoration: none;
  color:grey; 
}