/*
=============== 
Scroll
===============
*/
/* navbar */
nav {
  background: #fff;
  background: var(--clr-white);
  padding: 1rem 1.5rem;
}
/* fixed nav */
.fixed-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  background: var(--clr-white);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  box-shadow: var(--light-shadow);
}
.fixed-nav .links a {
  color: hsl(209, 61%, 16%);
  color: var(--clr-grey-1);
}
.fixed-nav .links a:hover {
  color: hsla(182, 63%, 54%);
  color: var(--clr-secondary);
}
/* .fixed-nav .links-container {
  height: auto !important;
} */
.nav-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.nav-toggle {
  font-size: 1.5rem;
  color: hsl(209, 61%, 16%);
  color: var(--clr-grey-1);
  background: transparent;
  border-color: transparent;
  transition: all 0.3s linear;
  transition: var(--transition);
  cursor: pointer;
}
.nav-toggle:hover {
  color: hsla(182, 63%, 54%);
  color: var(--clr-secondary);
}
.logo {
  height: 50px;
}
.links-container {
  height: 0;
  overflow: hidden;
  transition: all 0.3s linear;
  transition: var(--transition);
}
.show-links {
  height: 200px;
}
.links a {
  background: #fff;
  background: var(--clr-white);
  color: hsl(210, 22%, 49%);
  color: var(--clr-grey-5);
  font-size: 1.1rem;
  text-transform: capitalize;
  letter-spacing: 0.25rem;
  letter-spacing: var(--spacing);
  display: block;
  transition: all 0.3s linear;
  transition: var(--transition);
  font-weight: bold;
  padding: 0.75rem 0;
}
.links a:hover {
  color: hsl(209, 61%, 16%);
  color: var(--clr-grey-1);
}
@media screen and (min-width: 800px) {
  nav {
    background: transparent;
  }
  .nav-center {
    width: 90vw;
    max-width: 1170px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .nav-header {
    padding: 0;
  }
  .nav-toggle {
    display: none;
  }
  .links-container {
    height: auto !important;
  }
  .links {
    display: -webkit-flex;
    display: flex;
  }
  .links a {
    background: transparent;
    color: #fff;
    color: var(--clr-white);
    font-size: 1.1rem;
    text-transform: capitalize;
    letter-spacing: 0.25rem;
    letter-spacing: var(--spacing);
    display: block;
    transition: all 0.3s linear;
    transition: var(--transition);
    margin: 0 1rem;
    font-weight: bold;
    padding: 0;
  }
  .links a:hover {
    color: #fff;
    color: var(--clr-white);
  }
}
